.certifications__container {
    grid-template-columns: repeat(3, 270px);
    justify-content: center;
    column-gap: 1.8rem;
}

.certifications__content {
    position: relative;
    border: 1px solid black;
    background-color: var(--container-color);
    padding: 1rem 0 2rem 2.5rem;
    border-radius: 1.25rem;
}

.certifications__content:hover{
    /* transform: scale(1.05); */
    transition: 0.25s;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.514);
    border: 4.5px solid #cffa00;
    background-color: #faffef;
}

.certifications__icon {
    display: block;
    font-size: 2rem;
    color: var(--title-color);
    margin-bottom: var(--mb-1);
}

.certifications__title {
    font-size: var(--h3-font-size);
    margin-bottom: var(--mb-1);
    font-weight: var(--font-medium);
    text-align: center;
}



.certifications__button {
    color: var(--title-color);
    font-size: var(--small-font-size);
    display: inline-flex;
    align-items: center;
    column-gap: 0.25rem;
    cursor: pointer;
}

.certifications__button:hover {
    color: purple;
}

.certifications__button-icon {
    font-size: 1rem;
    transition: 0.3s;
}

.certifications__button:hover .certifications__button-icon{
    transform: translateX(00.25rem);
    color: yellow;
}

.certifications__modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: var(--z-modal);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 1rem;
    opacity: 0;
    visibility: hidden;
    transition: .3s;
}

.certifications__modal-content {
    width: 500px;
    position: relative;
    background-color: #faffef;
    padding: 4.5rem 2.5rem 2.5rem;
    border-radius: 1.5rem;
    border: 5px solid #cffa00;
}

.certifications__modal-close {
    position: absolute;
    top: 1.5rem;
    right: 1.5rem;
    font-size: 1.5rem;
    color: var(--title-color);
    cursor: pointer;
}

.certifications__modal-title {
    text-align: center;
}

.certifications__modal-description{
    text-align: start;
}

.certifications__modal-title {
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1);
}

.certifications__modal-description {
    font-size: var(--small-font-size);
    padding: 0 3.5rem;
    margin-bottom: var(--mb-2);
}

.certifications__modal-certifications {
    row-gap: 0.75rem;
}

.certifications__modal-data{
    display: flex;
    align-items: center;
    column-gap: .5rem;
}

.certifications__modal-icon {
    color: var(--title-color);
    font-size: 1.1rem;
}

.certifications__modal-info {
    font-size: var(--small-font-size);
}


/* Active Modal */

.active-modal {
    opacity: 1;
    visibility: visible;
}


/* BREAKPOINTS */
/* For Large Devices */
@media screen and (max-width: 992px) {
    .certifications__container{
        grid-template-columns: repeat(3, 218px);
    }
  }
  
  /* For Medium Devices */
  @media screen and (max-width: 768px) {
    .certifications__container{
        grid-template-columns: repeat(2, 200px);
    }

    .certifications__icon {
        font-size: 1.5rem;
    }
  }
  
  @media screen and (max-width: 576px) {
    .certifications__modal-content {
        padding: 4.5rem 1.5rem 2.5rem;
    }

    .certifications__modal-description {
        padding: 0;
    }
  }
  
  /* For Smaller Devices */
  @media screen and (max-width: 350px) {
    .certifications__container {
        grid-template-columns: max-content;
    }

    .certifications__content {
        padding-right: 6rem;
    }
  }